.menu {
  background-color: $c-light-pink;
  height: 100vh;
  box-sizing: border-box;

  &__list {
    margin: 0;
    padding: 132px 0 0;
    list-style: none;
    display: flex;
    gap: 24px;
    flex-direction: column;
    justify-content: end;
    align-items: center;
  }

  &__item {
    display: flex;

    &--phone {
      margin-right: 95px;
    }
  }

  &__link {
    color: $c-black;
    text-decoration: none;
    transition: color, 0.3s;

    &:hover {
      color: $c-accent-orange;
    }
  }

  &__icon {
    height: 18px;
    width: 18px;
    background-size: cover;
    background-repeat: no-repeat;

    &--phone {
      background-image: url(../images/phone.svg);
      text-decoration: none;

      &::after {
        content: "Contact us";
        position: relative;
        top: 50%;
        transform: translate(25%, -50%);
        display: block;
        width: max-content;
        color: $c-accent-orange;
      }
    }
  }
}
